import React from 'react'
import Header from '../components/Header'
import { graphql, Link } from 'gatsby';

const Layout = ({data}) => {
  const { edges } = data.allMarkdownRemark
  console.table(edges)
  return (
    <div>
      <Header />
      <div style={{
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        fontFamily: 'avenir'
      }}
      >
        {edges.map( edge => {
          const { frontmatter } = edge.node
          return (
            <div
              key={frontmatter.path}
              style={{marginBottom: '1rem'}}
            >
              <Link to={frontmatter.path}>
                {frontmatter.title}
              </Link>
            </div>
          )
        })}
      </div>
    </div>
  )
}

// Have to export this query so that Layout can receive data from props.
// The variable name `query` can be change to other things.
export const query = graphql`
  query {
    allMarkdownRemark (
      sort: { fields: [frontmatter___date], order: ASC }
    ) {
      edges {
        node {
          id
          frontmatter {
            title
            path
            date
            excerpt
          }
        }
      }
    }	  
  }
`

export default Layout
